import Share from 'ninelines-sharing';
import html2canvas from 'html2canvas';

if (document.querySelector('[data-social]')) {
    const list = document.querySelectorAll('[data-social]');
    const title = document.querySelector('.main-banner__title').textContent;
    const description = document.querySelector('.main-banner__description').textContent;
    const image = document.querySelector('meta[property="og:image"]');
    const ignor = document.querySelector('.main-banner__top');
    html2canvas(document.querySelector('.main-banner'), { backgroundColor: "#F1F1F1" }).then(function (canvas) {
        document.body.appendChild(canvas);
        image.setAttribute('content', canvas.toDataURL())
    });

    Array.prototype.forEach.call(list, (item) => {
        item.addEventListener('click', (e) => {
            const social = e.currentTarget.dataset.social;
            const url = location.origin + location.pathname;
            Share[social](url, null, title, description);
        });
    });
}
